@font-face{
    font-family: 'Mulish-Light';
    src: local('Mulish-Light'), url(./fonts/Mulish-Light.ttf) format('truetype');
    font-display: swap;
}
  
body {
    margin: 0;
    padding: 0;
    font-family: 'Mulish-Light', serif;
    color: #D9D9D9;
    background-color: #27292D;
}
  
* {
    box-sizing: border-box;
}

/* Foundations */
.text-bold{
  font-weight: bold
}

.text-torre {
  color: #D9D9D9;
}

.text-action-torre{
  color: #CCDB3A;
  text-decoration: underline;
}

.text-action-torre:hover{
  color: #CCDB3A;
  cursor: pointer;
}

.input-torre{
  background-color: rgba(0,0,0, 0.0);
  border-radius: 20px
}

.input-torre:focus{
  color: #D9D9D9;
  background-color: rgba(0,0,0, 0.0);
}

.second-text-torre {
  color: #CCDB3A;
}

.btn-cta-torre{
  background-color: #CCDB3A;
  color: white;
}

.btn-cta-torre:hover{
  color:white;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.btn-torre {
  background-color: #CCDB3A;
  color: #2D2E1E;
}

.bg-torre {
  background-color: #27292D;
}

.rounded-torre{
  border-radius: 3px
}
